
.dialog-title {
  font-size: 1rem;
  font-weight: bold;
  width: 100%;
  height: 52px;
  padding: 20px;
  border-bottom: thin solid #E0E0E0;
  box-sizing: border-box;
}

.share-dialog {
  .form-control {
    margin-top: 20px !important;
    display: block !important;
  }

  .dialog-content {
    text-align: center;
    padding: 20px !important;
    #preview-container, .generated-image, .share-controls {
      display: inline-block;
      box-sizing: border-box;
      vertical-align: middle;
    }

    #preview-container, .generated-image {
      margin: 0 5px;
    }
    .share-controls {
      margin: 0 10px;
      width: 200px;

      .image-share-btns {
        padding: 20px;
        .image-share-btn {
          font-weight: 500;
          opacity: 0.85;
        }

        .share-btn:hover, .share-btn:active {
          opacity: 1;
        }
      }
    }

    #preview-container {
      width: 268px;
    }

    .generated-image {
        width: 268px;
        height: 268px;

        img {
          max-width: 100%;
          max-height: 100%;
        }
    }

  }

  .dialog-actions {
    border-top: thin solid #E0E0E0;
    padding-right: 12px;
    & > * {
      margin: 0 4px !important;
    }
  }

  #preview {
    text-align: center;
    margin: auto;
    //zoom: 0.25;

    .gen-target {
      height: 100%;
      display: flex;
      flex-direction: column;
      position: relative;
    }
    .title, .subtitle, .plot-frame {
      background-color: #E6E6E6 !important;
    }
    .title, .subtitle {
      margin: 0 0.67em;
    }
    .title {
      font-size: 1em;
      font-weight: 500;
      margin-top: 0.4em;
    }
    .subtitle {
      font-size: 1em;
      margin-bottom: 0.25em;
    }
    .plot-frame {
      padding: 0 1.5em 1.5em 1.5em;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      height: 100%;

      .bg {
        background-color: #FAFBFC !important;
        -moz-box-shadow:    inset 0 0 4px #334d4d;
        -webkit-box-shadow: inset 0 0 4px #334d4d;
        box-shadow:         inset 0 0 4px #334d4d;
      }
    }
    .x-axis, .y-axis {
      position: absolute;
      font-size: 1em;
    }
    .x-axis {
      bottom: 0.1em;
      left: 3em;
    }
    .y-axis {
      transform: rotate(270deg);

      text-align: left;
      bottom: 5em;
      left: -2em;
    }
  }

  .row {
    justify-content: space-around;

    .MuiFormControl-root {
      margin-left: 10px;
      margin-right: 10px;
    }
  }

  .top-padded {
    margin-top: 0.5em;
  }

  .share-controls {
    text-align: center;

    .size-type-selector {
      flex-wrap: nowrap !important;
      flex-direction: row !important;
    }

    .size-inputs {
      margin-top: 1em;

      .custom-size-input {
        width: 4em;
        margin-right: 20px;
      }
    }
  }

  .caption {
    display: block;
    text-align: center;
    transform: scale(0.75);
    color: rgba(0, 0, 0, 0.6);
    margin: 0 0 5px 0;
  }

  .social-btns {
    display: flex;
    justify-content: space-between;
    margin-bottom: 7px;
    svg {
      width: 44px;
      height: 44px;
    }
  }

  .copy-btn {
    margin-top: -7px;
    margin-right: -15px;
  }
}
