$primary-color: #297ACC;
$controls-width: 220px;

* {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  background-color: #EAECEE !important;
}

#root {
  display: flex;
  flex-direction: column;
}

.router, .router > * {
  width: 100%;
  height: 100%;
}

#dashboard {
  flex: 1 1;
  padding: 17px 16px 8px 16px;
  box-sizing: border-box;
  width: 100%;
  display: block;
  float: left;
}

#dashboard main {
  display: flex;
  flex-direction: column;
}

.plot {
  position: relative;
}

.loading-plot {
  position: absolute;
  display: flex;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.5);
  opacity: 0.875;

  & > * {
    margin: auto;
  }
}

.timeline {
  display: flex;
  padding: 5px 1.5rem 0 0;
  box-sizing: border-box;
  align-items: start;
  .play {
    margin-right: 0.25rem;
    width: 41px;
    height: 41px;
    svg {
      width: 36px;
      height: 36px;
    }
  }
}

#dashboard aside {
  text-align: center;
  .controls {
    display: inline-block;
    width: $controls-width;
    padding-left: 16px;
    text-align: left;
    .plot-type {
      text-align: center;
      margin-top: 1px;
    }
  }
}

.form-control {
  margin-top: 20px !important;
  display: block !important;
}

.multiple-select-indeterminate {
  color: $primary-color;
}

.multiple-select-select-all {
  font-weight: 500;
}

.multiple-select-selected-all {
  background-color: rgba(0, 0, 0, 0.08);
  &:hover {
    background-color: rgba(0, 0, 0, 0.08);
  }
}

.grow {
  flex-grow: 1;
}

.section {
  height: 100%;
  width: 100%;
  padding: 10px;
  background-color: #FAFBFC !important;
  box-sizing: border-box;
}

.axis line {
  stroke: #000;
}

.axis text {
  fill: #000;
}

.tooltip {
  background-color: #FAFBFC !important;
  opacity: 0.95;
  z-index: 9999;  /* Appear over the app bar */
  padding: 0.6em !important;
}

.tooltip-content {
  color: #000;
  text-align: left;
  .tooltip-title {
    h3 {
      float: left;
      font-weight: 500;
      margin: 0;
      padding: 0.225em 0.5em 0.3em 0;
    }

    .year {
      float: right;
      padding: 0 0.5em;
      line-height: 1.55em;
      font-weight: 500;
      font-size: 0.95em;
      background-color: #4598f7;
      border-radius: 0.25em;
      color: white;
    }
  }

  .tooltip-body {
    clear: both;
    opacity: 0.75;
    line-height: 1.55em;
  }
}

.plot-area {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

#inner-plot-container {
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;

  .x-axis {
    text-align: center;
    position: absolute;
    bottom: -16px;
    width: 100%;
  }
  .y-axis {
    transform: rotate(270deg);
    transform-origin: left;
    text-align: center;
    position: relative;
    left: -7px;
    bottom: 3%;
  }
}

.plot-label {
  fill: #202122;
  opacity: 0.9;
  font-size: 0.9rem;
}

.MuiDrawer-root .MuiPaper-root {
  background-color: #EAECEE !important;
}

.side-menu {
  width: 270px;
  font-size: 1.2rem;
  color: #393939;

  a {
    display: flex;
    text-decoration: none;
    color: inherit;
  }

  .menu-item-icon {
    min-width: 46px;
    padding-left: 4px;
  }

  .nested-menu {
    .MuiList-padding {
      padding: 0.25rem 0;
    }
    .MuiListItem-root {
      padding-top: 0;
    }
    span {
      padding-left: 47px;
      font-size: 0.85rem;
    }
  }
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #3D3D3D !important;
  }
  .section, .gen-target {
    background-color: #303030 !important;
  }
  .axis line {
    stroke: #FFF;
  }
  .axis text, .plot-label {
    fill: #FFF;
  }
  .tooltip {
    background-color: #303030 !important;
    border: thin solid #717273;
    box-shadow: none;
  }
  .tooltip-content {
    color: #FFF;
  }
}

@media screen and (max-width: 639px) { /* Mobile */
  #dashboard main {
    height: 100vw;
    max-height: 420px;
  }
  #dashboard aside {
    padding-top: 10px;
  }
}

@media screen and (min-width: 640px) { /* Desktop */
  #root {
    height: 100%;
  }

  .top-bar {
    padding-right: $controls-width;
  }

  #dashboard main {
    width: 100%;
    height: 100%;
    float: left;
    margin-right: 0 - $controls-width;
    padding-right: $controls-width;
    box-sizing: border-box;
  }

  #dashboard aside {
    height: 100%;
  }

  #dashboard .controls > div {
    display: block;
  }
}
